import React from "react";
import { SimpleCell, Button, Div, Link } from "@vkontakte/vkui";

const GameItem = ({ id, go, nameGame, icon, namePanelGame, discription }) => {
  return (
    <SimpleCell
      before={
        <Div id={id} onClick={go} data-to={namePanelGame}>
          {icon}
        </Div>
      }
      // after={
      //   <Link id={id} mode="tertiary">
      //     –∫—É–ø–∏—Ç—å
      //   </Link>
      // }
      description={
        <>
          <Div
            style={{
              display: "flex",
              alignItems: "start",
              flexDirection: "column",
              justifyContent: "center",
              marginTop: 5,
              marginBottom: 5,
            }}
          >
            <Div style={{
              marginBottom: 8,
              whiteSpace: "pre-wrap"
              }}>{discription}</Div>
            <Div>
              <Button
                size="m"
                onClick={go}
                data-to={namePanelGame}
                style={{
                  backgroundColor: "#D40000",
                  color: "white",
                  borderRadius: "6px",
                  width: "120px",
                }}
              >
                –ò–≥—Ä–∞—Ç—å
              </Button>
            </Div>
          </Div>
        </>
      }
    >
      {nameGame}
    </SimpleCell>
  );
};

export { GameItem };
