import React from "react";
import {
  ModalRoot,
  ModalCard,
  InfoRow,
  FormLayout,
  Input,
} from "@vkontakte/vkui";

const ModalShopBuyItem = ({
  iconItem,
  nameItem,
  ratingItem,
  onChangeHandlerAmount,
  timeItem,
  buyItem,
  targetIdShop,
  setActiveModal,
}) => {
  return (
    <ModalRoot activeModal="info">
      <ModalCard
        id="info"
        onClose={() => setActiveModal(null)}
        icon={iconItem}
        header={nameItem}
        caption={
          <>
            {targetIdShop === 1 ? (
              <>
                <InfoRow>
                  {nameItem} - –ø—Ä–∏ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏–∏ —Å—Ç–∞–≤–∏—Ç +{ratingItem} Like.
                </InfoRow>
                <FormLayout>
                  <Input
                    top={`–£–∫–∞–∂–∏—Ç–µ –∫–æ–ª-–≤–æ ${nameItem}`}
                    placeholder="1"
                    onChange={onChangeHandlerAmount}
                    type="number"
                  />
                </FormLayout>
              </>
            ) : (
              <>
                <InfoRow>
                  {nameItem} - –ø—Ä–∏ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏–∏ —Å—Ç–∞–≤–∏—Ç -{ratingItem} Dislike.
                </InfoRow>
                <FormLayout>
                  <Input
                    top={`–£–∫–∞–∂–∏—Ç–µ –∫–æ–ª-–≤–æ ${nameItem}`}
                    placeholder="1"
                    onChange={onChangeHandlerAmount}
                    type="number"
                  />
                </FormLayout>
              </>
            )}
          </>
        }
        actions={[
          {
            title: "–ö—É–ø–∏—Ç—å",
            mode: "commerce",
            action: () => {
              buyItem();
            },
          },
        ]}
      ></ModalCard>
    </ModalRoot>
  );
};

export { ModalShopBuyItem };
