import React from "react";
import { useState, useEffect } from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
  PullToRefresh,
  Button,
} from "@vkontakte/vkui";
import bridge from "@vkontakte/vk-bridge";

import { TopUsersItem, TopGroupsItem } from "../items";
import { dataTopUsers, dataTopGroups } from "../data";
import {
  formatCoins,
  shop,
  lootBox,
  stickers,
  mining,
  SocketManager,
  formatDate,
} from "../helpers";

const { callMethod } = SocketManager;

const Top = ({
  ranks,
  id,
  userPlace,
  rating,
  go,
  vk,
  snackbar,
  openAlert,
  scheme,
  $useItem,
  openMenuVIP
}) => {
  const [userToken, setUserToken] = useState(null);
  const [activeTab, setActiveTab] = useState("general");
  const [friendsTop, setFriendsTop] = useState(null);
  const [renderFriends, setRenderFriends] = useState([]);
  const [fetching, setFetching] = useState(false);
  const [renderGlobal, setRenderGlobal] = useState(0);
  const [renderGroups, setRenderGroups] = useState(0);
  const [sliceItemUsers, setSliceItemUsers] = useState(50);
  const [sliceItemGroups, setSliceItemGroups] = useState(25);

  let groupsTop = [];
  let targetIdGroup = 0;

  groupsTop = dataTopGroups;

  useEffect(() => {
    getUsers();
    getGroups();
  }, []);

  function addGroupTop(e) {
    targetIdGroup = e.currentTarget.id;
    let urlGroup = groupsTop[targetIdGroup - 1].id;
    console.log("urlGroup", urlGroup);
    bridge
      .sendPromise("VKWebAppJoinGroup", {
        group_id: urlGroup,
      })
      .then((res) => {
        if (res.result) {
          openAlert("–í—ã —É–∂–µ –ø–æ–¥–ø–∏—Å–∞–Ω—ã –Ω–∞ —ç—Ç—É –≥—Ä—É–ø–ø—É", "green");
        }

        console.log("res", res.result);
      })
      .catch((err) => {
        if (err.error_data.error_reason === "User can not join this group") {
          openAlert("–ü–æ–¥–∞–π—Ç–µ –∑–∞—è–≤–∫—É, —á—Ç–æ–±—ã –≤—Å—Ç—É–ø–∏—Ç—å –≤ –≥—Ä—É–ø–ø—É", "red");
        }
        console.log("err", err);
      });
  }

  function tabGeneral() {
    setActiveTab("general");
    window.scrollTo(0, 0);
  }

  function tabFriends() {
    setActiveTab("friends");
    getFriendsAccess();
    window.scrollTo(0, 0);
  }

  function tabGroups() {
    setActiveTab("groups");
    window.scrollTo(0, 0);
  }

  function getUsers() {
    callMethod("getUsersTop").then(({ status, message }) => {
      if (status === "success") {
        console.log("getUsersTop", message);
        setRenderGlobal(
          message.map((item, index) => {
            return (
              <TopUsersItem
                key={index}
                id={index + 1}
                idVk={item.id}
                isVIP={item.isVIP}
                vipNameColor={item.vipNameColor}
                photoURL={item.photoURL}
                firstName={item.firstName}
                lastName={item.lastName}
                rating={item.rating}
                amount={item.pornCoins}
                ranks={ranks || []}
                likes={item.likes}
                dislikes={item.dislikes}
                scheme={scheme}
                $useItem={$useItem}
                openMenuVIP={openMenuVIP}
              />
            );
          })
        );
      }
      setFetching(false);
    });
  }

  function getGroups() {
    callMethod("getGroupsTop").then(({ status, message }) => {
      // console.log({ message, status });
      if (status === "success") {
        groupsTop = message;
        // console.log("setGroupsTop", groupsTop);
        setRenderGroups(
          message.map((item, index) => {
            return (
              <TopGroupsItem
                key={index}
                id={index + 1}
                idVk={item.id}
                photoURL={item.photoURL}
                name={item.name}
                rating={item.rating}
                addGroupTop={addGroupTop}
                likes={item.likes}
                dislikes={item.dislikes}
                $useItem={$useItem}
              />
            );
          })
        );
      }

      setFetching(false);
    });
  }

  function onRefresh() {
    // –í–´–ó–í–ê–¢–¨ –ó–ê–ü–†–û–° –î–ê–ù–ù–´–• –î–õ–Ø –û–¢–î–ï–õ–¨–ù–û–ì–û –¢–û–ü–ê , –û–¢–û–®–ï–õ –ù–ê–õ–ò–¢–¨ –ß–ê–Ø
    setFetching(true);
    switch (activeTab) {
      case "general":
        getUsers();
        break;
      case "friends":
        getFriendsAccess();
        break;
      case "groups":
        getGroups();
        break;
      default:
        break;
    }
  }

  function getFriendsAccess() {
    bridge
      .sendPromise("VKWebAppGetAuthToken", {
        scope: "friends",
        app_id: 7523466,
      })
      .then((res) => {
        if (res.access_token) {
          setUserToken(res.access_token);
          getAndSendToServerFriends(res.access_token);
        } else {
          // TODO: –°–¥–µ–ª–∞—Ç—å –æ—à–∏–±–∫—É –æ —Ç–æ–º, —á—Ç–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–µ –ø—Ä–µ–¥–æ—Å—Ç–∞–≤–∏–ª –¥–æ—Å—Ç—É–ø –∫ –¥—Ä—É–∑—å—è–º
        }
      })
      .catch((err) => {
        // vk.log(err);
        console.log(err);
      });
  }

  function getAndSendToServerFriends(userToken) {
    bridge
      .sendPromise("VKWebAppCallAPIMethod", {
        method: "friends.get",
        params: {
          access_token: userToken,
          v: "5.103",
        },
      })
      .then((res) => {
        if (res.response) {
          callMethod("getFriendsTop", {
            items: res.response.items,
          }).then(({ message }) => {
            if (!message.length) {
              return setFriendsTop("–£ –í–∞—Å –Ω–µ—Ç –¥—Ä—É–∑–µ–π –≤ –∏–≥—Ä–µ");
            }
            console.log(message);
            setFriendsTop(true);
            setFetching(false);
            setRenderFriends(
              message.map((item, index) => {
                return (
                  <TopUsersItem
                    key={index}
                    id={index + 1}
                    idVk={item.id}
                    isVIP={item.isVIP}
                    photoURL={item.photoURL}
                    vipNameColor={item.vipNameColor}
                    firstName={item.firstName}
                    lastName={item.lastName}
                    rating={item.rating}
                    amount={item.pornCoins}
                    ranks={ranks || []}
                    likes={item.likes}
                    dislikes={item.dislikes}
                    scheme={scheme}
                    $useItem={$useItem}
                    openMenuVIP={openMenuVIP}
                  />
                );
              })
            );
          });
        } else {
          return setFriendsTop("–û—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ –¥—Ä—É–∑–µ–π");
        }
      })
      .catch((err) => {
        return setFriendsTop("–û—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ –¥—Ä—É–∑–µ–π");
      });
  }

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple left={<PanelHeaderBack onClick={go} data-to="home" />}>
        –¢–æ–ø
      </PanelHeaderSimple>

      <>
        <FixedLayout vertical="top" filled={true}>
          <Tabs>
            <TabsItem onClick={tabGeneral} selected={activeTab === "general"}>
              –û–±—â–∏–π
            </TabsItem>
            <TabsItem onClick={tabFriends} selected={activeTab === "friends"}>
              –î—Ä—É–∑—å—è
            </TabsItem>
            <TabsItem onClick={tabGroups} selected={activeTab === "groups"}>
              –°–æ–æ–±—â–µ—Å—Ç–≤–∞
            </TabsItem>
          </Tabs>
        </FixedLayout>

        <>
          {activeTab === "general" ? (
            <>
              {renderGlobal.length ? (
                <>
                  <Div className="panelTopHeader flexStartRowCenter">
                    –í–∞—à–µ –º–µ—Å—Ç–æ: {userPlace}
                  </Div>
                  <PullToRefresh
                    onRefresh={onRefresh}
                    isFetching={fetching}
                    style={{ marginTop: 10 }}
                  >
                    <Div>{renderGlobal.slice(0, sliceItemUsers)}</Div>
                    {sliceItemUsers === 100 ? (
                      <></>
                    ) : (
                      <Div
                        style={{
                          marginTop: "15px",
                          marginBottom: "15px",
                          marginLeft: "10px",
                          marginRight: "10px",
                        }}
                      >
                        <Button
                          size="l"
                          stretched
                          onClick={() => setSliceItemUsers(sliceItemUsers + 25)}
                          style={{
                            backgroundColor: "green",
                            color: "white",
                          }}
                        >
                          –ü–æ–∫–∞–∑–∞—Ç—å –µ—â—ë
                        </Button>
                      </Div>
                    )}
                  </PullToRefresh>
                </>
              ) : (
                <div className="flexCenterColumn">
                  <Spinner size="large" style={{ marginTop: 90 }} />
                </div>
              )}
            </>
          ) : (
            ""
          )}

          {activeTab === "friends" ? (
            <div style={{ marginTop: 55 }}>
              {friendsTop === null ? (
                <div className="flexCenterColumn">
                  <Spinner size="large" style={{ marginTop: 90 }} />
                </div>
              ) : !renderFriends.length ? (
                <Div>{friendsTop}</Div>
              ) : (
                <PullToRefresh
                  onRefresh={onRefresh}
                  isFetching={fetching}
                  style={{ marginTop: 10 }}
                >
                  <Div>{renderFriends}</Div>
                </PullToRefresh>
              )}
            </div>
          ) : (
            ""
          )}

          {activeTab === "groups" ? (
            <div style={{ marginTop: 55 }}>
              {renderGroups.length ? (
                <PullToRefresh
                  onRefresh={onRefresh}
                  isFetching={fetching}
                  style={{ marginTop: 10 }}
                >
                  <Div>{renderGroups.slice(0, sliceItemGroups)}</Div>
                  {renderGroups.length > 25 ? (
                    <Div
                      style={{
                        marginTop: "15px",
                        marginBottom: "15px",
                        marginLeft: "10px",
                        marginRight: "10px",
                      }}
                    >
                      <Button
                        size="l"
                        stretched
                        onClick={() => setSliceItemGroups(sliceItemGroups + 25)}
                        style={{
                          backgroundColor: "green",
                          color: "white",
                        }}
                      >
                        –ü–æ–∫–∞–∑–∞—Ç—å –µ—â—ë
                      </Button>
                    </Div>
                  ) : (
                    <></>
                  )}
                </PullToRefresh>
              ) : (
                <div className="flexCenterColumn">
                  <Spinner size="large" style={{ marginTop: 90 }} />
                </div>
              )}
            </div>
          ) : (
            ""
          )}
        </>

        {snackbar}
      </>
    </Panel>
  );
};

export { Top };
