import React from "react";
import { SimpleCell, Cell, Button, Div, Link, Avatar } from "@vkontakte/vkui";
import Icon12Verified from "@vkontakte/icons/dist/12/verified";

const RouletteItem = ({
  id,
  name,
  photoUrl,
  go,
  userUrl,
  amount,
  percent,
  isVIP,
  vipNameColor,
  openMenuVIP
}) => {
  return (
    <Cell
      before={
        photoUrl ? (
          <Link href={userUrl} target="_blank">
            <Avatar
              size={48}
              src={photoUrl}
              // src={getAvatarUrl('user_xyz')}
            />
          </Link>
        ) : (
          <Avatar src={"https://vk.com/images/camera_200.png"} />
        )
      }
      // before={
      //   <Div
      //     id={id}
      //     // onClick={() => openModalStickers(price, id)}
      //   >
      //     {photo}
      //   </Div>
      // }
      // after={
      //   <Link id={id} mode="tertiary">
      //     –∫—É–ø–∏—Ç—å
      //   </Link>
      // }
      description={
        <>
          <Div
            style={{
              display: "flex",
              alignItems: "center",
              flexDirection: "row",
              justifyContent: "start",
              marginTop: 5,
              marginLeft: 10,
              marginBottom: 5,
            }}
          >
            <Div
              // size="m"
              // onClick={() => qiwiPay(price, id)}
              // onClick={go}
              // data-to={namePanelGame}
              style={{
                backgroundColor: "green",
                color: "white",
                borderRadius: "6px",
                width: "100%",
                height: "25px",
                display: "flex",
                alignItems: "center",
                justifyContent: "space-between",
                flexDirection: "row",
                userSelect: "none",
                paddingLeft: "15px",
                paddingRight: "15px",
              }}
            >
              <Div
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "space-around",
                  flexDirection: "row",
                  userSelect: "none",
                }}
              >
                <Div style={{ userSelect: "none" }}> {amount} </Div>

                <Div className="iconPornCoin16px" style={{ marginLeft: 5 }} />
              </Div>

              <Div style={{ marginLeft: 5 }}> ~ {percent} % </Div>
            </Div>
          </Div>
        </>
      }
    >
      <Div
        style={{
          marginLeft: 10,
          display: "flex",
          justifyContent: "start",
          alignItems: "center",
          flexDirection: "row",
        }}
      >
        <span
          style={{
            color: [, "green", "#D40000", "blue", "#800080"][vipNameColor],
          }}
        >
          {name}
        </span>
        {vipNameColor === 0 ? (
          ""
        ) : (
          <Icon12Verified
            onClick={openMenuVIP}
            style={{ marginLeft: "5px" }}
            className="animationIcons"
            fill={[, "green", "#D40000", "blue", "#800080"][vipNameColor]}
            width={16}
            height={16}
          />
        )}
      </Div>
    </Cell>
  );
};

export { RouletteItem };
