import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
  Button,
  Group,
  Link,
  Avatar,
  isVIP,
} from "@vkontakte/vkui";

import { IntuitionItem, ModalIntuitionResult } from "../gamesItem";
import { ModalIntuition } from "../modal";
import {
  formatCoins,
  SocketManager,
  dataGames,
  formatCoinsToServer,
  delay,
} from "../helpers";
const { callMethod } = SocketManager;

let interval = null;
let events = false;

const Intuition = ({
  id,
  go,
  snackbar,
  pornCoins,
  openBuyPornCoins,
  setActiveModal,
  openAlert,
  openPopoutSharing,
  openMenuVIP,
  getPlatform
}) => {
  let number = "";
  let numberResult = 10;
  let globalBank = 0;
  let min = 0;
  let max = 300;
  let num = 0;
  let game = true;
  // let numberItem = 0;
  const [dataBet, setDataBet] = useState([]);
  const [bank, setBank] = useState(0);
  const [screenBank, setScreenBank] = useState("0");
  const [timer, setTimer] = useState(300);
  const [screenTimer, setScreenTimer] = useState("00:00");
  const [numberItem, setNumberItem] = useState(0);
  const [randomNumber, setRandomNumber] = useState(0);
  const [winners, setWinners] = useState([]);
  const [renderItemResult, setRenderItemResult] = useState([]);
  const [stateGame, setStateGame] = useState(true);

  function convertTime(sec) {
    return (
      `${Math.floor(sec / 60)}`.padStart(2, 0) +
      ":" +
      `${sec % 60}`.padStart(2, 0)
    );
  }

  useEffect(() => {
    setTimeout(() => {
      callMethod("enterIntuition", {
        cancel: false,
      });
    }, 2000);

    SocketManager.socket.on("betsList2", (bets) => {
      if (!game) return;

      let b = 0;
      bets.forEach((bet) => {
        b += bet.amount;
      });
      globalBank = b / 1_000;
      if (b !== 0) {
        setBank(b / 1_000);
      }
      setScreenBank((b / 1_000).toLocaleString());

      setDataBet(
        bets.map((bet) => {
          return {
            idVk: bet.user.id,
            name: bet.user.fullName,
            photoUrl: bet.user.photoURL,
            amount: bet.amount,
            inputNumber: bet.inputNumber,
            isVIP: bet.user.isVIP,
            vipNameColor: bet.user.vipNameColor,
          };
        })
      );
    });

    SocketManager.socket.on("summarize2", ({ winners, randomNumber, bank }) => {
      game = false;
      // setStateGame(false);

      console.log("–ü–æ–±–µ–¥–∏—Ç–µ–ª–∏:", winners);
      numberResult = randomNumber;
      // setWinners(winners);
      console.log("randomNumber", randomNumber);

      if (winners !== null) {
        let htmlWinners = winners.map((item, index) => {
          const userUrl = `https://vk.com/id${item.user.id}`;
          const name = item.user.fullName;
          return (
            <Div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                flexDirection: "column",
                userSelect: "none",
              }}
            >
              <Div style={{ marginLeft: 10, marginRight: 10 }}>
                <Link href={userUrl} target="_blank">
                  <Avatar size={48} src={item.user.photoURL} />
                </Link>
              </Div>
              <Div>{name.split(" ")[0]}</Div>
              {/* <Div style={{whiteSpace: "nowrap", width: "70px", marginLeft: "10px"}}>{name}</Div> */}
            </Div>
          );
        });

        console.log("htmlWinners", htmlWinners);

        const timerId = setInterval(() => {
          if (num <= randomNumber) {
            // console.log("numberItem:", num);
            setNumberItem(num++);
          } else {
            num = 0;
            modalIntuitionResult(htmlWinners, bank);
            getBetsList(1);
            game = true;
            // setStateGame(true);
            clearInterval(timerId);
            delay(3000).then(() => {
              setNumberItem(0);
            });
            console.log("–≤—ã–∑–≤–∞–ª–∏ clearInterval");
          }
        }, 50);
      } else {
        num = 0;
        getBetsList(1);
        game = true;
        // setStateGame(true);
      }
    });

    getBetsList(2);
  }, []);

  useEffect(() => {
    if (interval) {
      console.log("–ò–Ω—Ç–µ—Ä–≤–∞–ª —É–∂–µ –µ—Å—Ç—å!");
      clearInterval(interval);
    }

    let seconds = timer;

    setScreenTimer(convertTime(seconds));

    interval = setInterval(() => {
      seconds--;

      if (seconds <= 0) {
        clearInterval(interval);
      }

      setScreenTimer(convertTime(seconds));
    }, 1000);
  }, [timer]);

  function getBetsList(a) {
    let hasAnswer = false;

    setTimeout(() => {
      if (!hasAnswer) {
        getBetsList(3);
      }
    }, 3_000);
    
    callMethod("getIntuitionBetsList", a).then(({ message }) => {
      hasAnswer = true;
      setTimer(0);
      setTimer(message.timer - 1); // –°–∫–æ–ª—å–∫–æ –æ—Å—Ç–∞–ª–æ—Å—å –¥–æ –∏—Ç–æ–≥–∞ –≤ —Å–µ–∫—É–Ω–¥–∞—Ö

      let b = 0;
      message.bets.forEach((bet) => {
        b += bet.amount;
      });
      globalBank = b / 1_000;
      if (b !== 0) {
        setBank(b / 1_000);
      }
      setScreenBank((b / 1_000).toLocaleString());

      setDataBet(
        message.bets.map((bet) => {
          return {
            idVk: bet.user.id,
            name: bet.user.fullName,
            photoUrl: bet.user.photoURL,
            amount: bet.amount,
            inputNumber: bet.inputNumber,
            isVIP: bet.user.isVIP,
            vipNameColor: bet.user.vipNameColor,
          };
        })
      );
    });
  }

  function onChangeAction(e) {
    const value = e.target.value.trim();
    // setBet(+value);
    number = +value;
    console.log("Number.isInteger(number)", Number.isInteger(number));
  }

  const makeBet = () => {
    // console.log("typeof number", typeof number);
    if (number < 0 || number > 300) {
      setActiveModal(null);
      return openAlert(
        number < 0
          ? "–í—ã –≤–≤–µ–ª–∏ —á–∏—Å–ª–æ –º–µ–Ω—å—à–µ 0, –ø–∏–ª—é–ª—é –∑–∞–±—ã–ª –ø—Ä–∏–Ω—è—Ç—å?"
          : "–í—ã –≤–≤–µ–ª–∏ —á–∏—Å–ª–æ –±–æ–ª—å—à–µ 300, –ø–∏–ª—é–ª—é –∑–∞–±—ã–ª –ø—Ä–∏–Ω—è—Ç—å?",
        "red"
      );
    }
    if (Number.isInteger(number) === false) {
      setActiveModal(null);
      return openAlert(
        "–í—ã –≤–≤–µ–ª–∏ –Ω–µ —Ü–µ–ª–æ–µ —á–∏—Å–ª–æ, –≤–≤–µ–¥–∏—Ç–µ –¶–ï–õ–û–ï —á–∏—Å–ª–æ –æ—Ç 0 –¥–æ 300",
        "red"
      );
    }
    if (isNaN(number)) {
      setActiveModal(null);
      return openAlert("–í—ã –≤–≤–µ–ª–∏ –Ω–µ —á–∏—Å–ª–æ, –≤–≤–µ–¥–∏—Ç–µ —á–∏—Å–ª–æ –æ—Ç 0 –¥–æ 300", "red");
    }

    setActiveModal(null);
    callMethod("addIntuitionBet", {
      inputNumber: number,
    }).then(({ status, message }) => {
      // setActiveModal(null);
      if (status === "req_limit") return;
      openAlert(message, status === "denied" ? "red" : "green");
    });
  };

  const renderIntuition = dataBet.map((item, index) => {
    const userUrl = `https://vk.com/id${item.idVk}`;

    return (
      <IntuitionItem
        key={index}
        id={index + 1}
        name={item.name}
        photoUrl={item.photoUrl}
        userUrl={userUrl}
        number={number}
        go={go}
        inputNumber={item.inputNumber}
        isVIP={item.isVIP}
        vipNameColor={item.vipNameColor}
        openMenuVIP={openMenuVIP}
      />
    );
  });

  function openIntuitionModal() {
    setActiveModal(
      <ModalIntuition
        setActiveModal={setActiveModal}
        onChangeAction={onChangeAction}
        makeBet={makeBet}
      />
    );
  }

  const modalIntuitionResult = (a, bank) => {
    bank = (bank / 1_000).toLocaleString();
    setActiveModal(
      <ModalIntuitionResult
        setActiveModal={setActiveModal}
        onChangeAction={onChangeAction}
        openPopoutSharing={openPopoutSharing}
        bank={bank}
        numberResult={numberResult}
        renderItemResult={a}
      />
    );
  };

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple
        left={
          <Div
            style={{
              display: "flex",
              alignItems: "center",
              flexDirection: "row",
              justifyContent: "center",
            }}
          >
            <PanelHeaderBack
              onClick={(e) => {
                go(e);

                callMethod("enterIntuition", {
                  cancel: true,
                });
                SocketManager.socket.removeEventListener("betsList2");
                SocketManager.socket.removeEventListener("summarize2");
              }}
              data-to="games"
            />

            <Button
              style={{
                backgroundColor: "green",
                marginLeft: "10px",
                padding: "2px",
              }}
              mode="commerce"
              size="m"
              onClick={openBuyPornCoins}
            >
              –ü–æ–ø–æ–ª–Ω–∏—Ç—å
            </Button>
            <Button
              style={{
                backgroundColor: "green",
                marginLeft: "10px",
                padding: "2px",
              }}
              mode="commerce"
              size="m"
            >
              {/* 00:{seconds} —Å–µ–∫ */}
              {screenTimer}
            </Button>
          </Div>
        }
      ></PanelHeaderSimple>

      <>
        <Div
          style={{
            padding: "20px 20px 5px 20px",
          }}
        >
          <FixedLayout
            style={{
              // display: "flex",
              // alignItems: "space-between",
              // flexDirection: "column",
              // justifyContent: "center",
              // backgroundColor: "white",
              // height: "50px",
              marginTop: 50,
            }}
            vertical="top"
            filled
          >
            <Div
              style={{
                display: "flex",
                alignItems: "center",
                flexDirection: "column",
                justifyContent: "center",
                height: "50px",
              }}
            >
              <Div
                style={{
                  display: "flex",
                  alignItems: "center",
                  flexDirection: "row",
                  justifyContent: "center",
                  color: "green",
                  backgroundColor: "green",
                  borderRadius: "6px",
                  padding: "3px",
                  color: "white",
                  width: "95%",
                  height: "30px",
                  // marginRight: 10,
                }}
              >
                <Div style={{ userSelect: "none" }}>
                  {" "}
                  –ë–∞–ª–∞–Ω—Å: {formatCoins(pornCoins)}
                </Div>
                <Div className="iconPornCoin16px" style={{ marginLeft: 5 }} />
              </Div>
            </Div>
          </FixedLayout>

          <Div
            style={{
              marginTop: "30px",
              display: "flex",
              alignItems: "center",
              flexDirection: "column",
              justifyContent: "center",
            }}
          >
            <Div
              style={{
                display: "flex",
                alignItems: "center",
                flexDirection: "column",
                justifyContent: "center",
                marginBottom: "10px",
                backgroundColor: "green",
                borderRadius: "6px",
                color: "white",
                height: "30px",
                width: "200px",
              }}
            >
              –ß–∏—Å–ª–æ –ø–æ–±–µ–¥–∏—Ç–µ–ª—è: {numberItem}
            </Div>
          </Div>

          <Div
            style={{
              // marginTop: 30,
              height: getPlatform === "web" ? "200px" : "420px",
              overflowY: "scroll",
            }}
          >
            {renderIntuition.length ? (
              <>{renderIntuition}</>
            ) : (
              <div
                style={{
                  display: "flex",
                  alignItems: "center",
                  flexDirection: "column",
                }}
              >
                <Spinner
                  size="large"
                  style={{ marginTop: 90, marginBottom: "10px" }}
                />
                <div>–ß—Ç–æ–±—ã –Ω–∞—á–∞—Ç—å, —Å–¥–µ–ª–∞–π —Å—Ç–∞–≤–∫—É!</div>
              </div>
            )}
          </Div>

          <FixedLayout vertical="bottom" filled>
            <Div
              style={{
                display: "flex",
                alignItems: "center",
                flexDirection: "column",
                justifyContent: "space-evenly",
                // marginBottom: 5,
                height: "90px",
                // backgroundColor: "white",
              }}
            >
              <Div>
                <Button
                  size="m"
                  // disabled={stateGame ? false : true}
                  // onClick={() => qiwiPay(price, id)}
                  onClick={() => openIntuitionModal()}
                  // data-to={namePanelGame}
                  style={{
                    backgroundColor: "green",
                    color: "white",
                    borderRadius: "6px",
                    width: "140px",
                    padding: 2,
                  }}
                >
                  –°–¥–µ–ª–∞—Ç—å —Å—Ç–∞–≤–∫—É
                </Button>
              </Div>

              <Div
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  flexDirection: "row",
                  color: "white",
                  backgroundColor: "green",
                  width: "95%",
                  padding: "3px",
                  borderRadius: "6px",
                  height: "30px",
                }}
              >
                <Div style={{ userSelect: "none" }}>–í –±–∞–Ω–∫–µ: {screenBank} </Div>
                <Div className="iconPornCoin16px" style={{ marginLeft: 5 }} />
              </Div>
            </Div>
          </FixedLayout>
        </Div>

        {snackbar}
      </>
    </Panel>
  );
};

export { Intuition };
