import React, { useState, useEffect, useReducer } from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  Group,
  Header,
  Cell,
  Div,
  Input,
  Link,
  Button,
  Checkbox,
  FixedLayout,
  Tabs,
  TabsItem,
} from "@vkontakte/vkui";

import { SocketManager, formatCoinsToServer } from "../helpers";
const { callMethod } = SocketManager;

const AdminPanel = ({ go, id, openAlert, snackbar }) => {
  const [DC, setDC] = useState("");
  const [userId, setUserId] = useState("");
  const [isBanned, setIsBanned] = useState(false);
  const [operation, setOperation] = useState(false);
  const [activeTab, setActiveTab] = useState("ban");

  function tab(type) {
    switch (type) {
      case "ban":
        setActiveTab(type);
        break;
      case "transfer":
        setActiveTab(type);
        break;
    }
  }

  function banUser() {
    if (!userId) {
      openAlert("–í—ã –Ω–µ –≤–≤–µ–ª–∏ —Å—Å—ã–ª–∫—É –Ω–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è", "red");
    }

    callMethod("banUser", {
      userId,
      isBanned,
    }).then(({ message, status }) => {
      if (status === 'req_limit') return;
      openAlert(message, status === "denied" ? "red" : "green");
    });
  }

  function transfer() {
    let pornCoins = formatCoinsToServer(DC);
    callMethod('transfer', {
      pornCoins: operation ? pornCoins : -pornCoins,
      userId
    }).then(({ message, status }) => {
      if (status === 'req_limit') return;
      openAlert(message, status === "denied" ? "red" : "green");
    })
  }

  function onChangeAction(e, type) {
    const value = e.target.value.trim();

    switch (type) {
      case "userId":
        setUserId(value);
        break;
      case "DC":
        setDC(value);
        break;
    }
  }

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple left={<PanelHeaderBack onClick={go} data-to="home" />}>
        –ê–¥–º–∏–Ω –ø–∞–Ω–µ–ª—å 3000
      </PanelHeaderSimple>

      <FixedLayout vertical="top" filled={true}>
        <Tabs>
          <TabsItem
            onClick={() => {
              tab("ban");
            }}
            selected={activeTab === "ban"}
          >
            –ó–∞–±–∞–Ω–∏—Ç—å
          </TabsItem>
          <TabsItem
            onClick={() => {
              tab("transfer");
            }}
            selected={activeTab === "transfer"}
          >
            –ü–µ—Ä–µ–≤–µ—Å—Ç–∏
          </TabsItem>
        </Tabs>
      </FixedLayout>

      <Div>
        {activeTab === "ban" ? (
          <Group
            style={{ marginTop: "55px" }}
            header={<Header mode="secondary">–í–≤–µ–¥–∏—Ç–µ —Å—Å—ã–ª–∫—É –Ω–∞ —à–∏–∑–æ–∏–¥–∞</Header>}
            // separator="show"
          >
            <Cell>
              <Input
                // value={inputpornCoins}
                onChange={(e) => onChangeAction(e, "userId")}
                type="text"
              ></Input>
              <Div
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "space-around",
                  flexDirection: "row",
                }}
              >
                <Checkbox
                  onChange={() => {
                    setIsBanned(true);
                  }}
                  checked={isBanned}
                >
                  –ó–∞–±–∞–Ω–∏—Ç—å
                </Checkbox>
                <Checkbox
                  onChange={() => {
                    setIsBanned(false);
                  }}
                  checked={!isBanned}
                >
                  –†–∞–∑–±–∞–Ω–∏—Ç—å
                </Checkbox>
              </Div>

              <Button
                style={{
                  backgroundColor: "red",
                  color: "white",
                  marginTop: "10px",
                }}
                onClick={banUser}
                mode="commerce"
                size="xl"
                stretched
              >
                –í—ã–ø–æ–ª–Ω–∏—Ç—å
              </Button>
            </Cell>
          </Group>
        ) : (
          <>
            <Group
              style={{ marginTop: "55px" }}
              header={
                <Header mode="secondary">–í–≤–µ–¥–∏—Ç–µ —Å—Å—ã–ª–∫—É –Ω–∞ —à–∏–∑–æ–∏–¥–∞</Header>
              }
              separator="hide"
            >
              <Cell>
                <Input
                  // value={inputpornCoins}
                  onChange={(e) => onChangeAction(e, "userId")}
                  type="text"
                ></Input>
              </Cell>
            </Group>
            <Group
              header={
                <Header mode="secondary">–í–≤–µ–¥–∏—Ç–µ –∫–æ–ª-–≤–æ pornCoins</Header>
              }
            >
              <Cell>
                <Input
                  // value={inputpornCoins}
                  onChange={(e) => onChangeAction(e, "DC")}
                  type="text"
                ></Input>
                <Div
                  style={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "space-around",
                    flexDirection: "row",
                  }}
                >
                  <Checkbox
                    onChange={() => {
                      setOperation(true);
                    }}
                    checked={operation}
                  >
                    –ü–µ—Ä–µ–≤–µ—Å—Ç–∏ DC
                  </Checkbox>
                  <Checkbox
                    onChange={() => {
                      setOperation(false);
                    }}
                    checked={!operation}
                  >
                    –°–Ω—è—Ç—å DC
                  </Checkbox>
                </Div>

                <Button
                  style={{
                    backgroundColor: "red",
                    color: "white",
                    marginTop: "10px",
                  }}
                  onClick={transfer}
                  mode="commerce"
                  size="xl"
                  stretched
                >
                  –í—ã–ø–æ–ª–Ω–∏—Ç—å
                </Button>
              </Cell>
            </Group>
          </>
        )}
      </Div>
      {snackbar}
    </Panel>
  );
};

export { AdminPanel };
