import React from "react";
import { Cell, Div, Avatar, Link } from "@vkontakte/vkui";
import Icon12Fire from "@vkontakte/icons/dist/12/fire";
import Icon16LikeOutline from "@vkontakte/icons/dist/16/like_outline";
import Icon16Like from "@vkontakte/icons/dist/16/like";
import Icon12Verified from "@vkontakte/icons/dist/12/verified";
import { getRankByRating, formatCoins } from "../helpers";

const TopUsersItem = ({
  id,
  firstName,
  lastName,
  rating,
  photoURL,
  amount,
  idVk,
  ranks,
  isVIP,
  vipNameColor,
  likes,
  dislikes,
  scheme,
  $useItem,
  openMenuVIP,
}) => {
  const urlUser = "https://vk.com/id" + idVk;

  return (
    <Div
      className={
        scheme === "bright_light"
          ? "itemShop_bright_light"
          : "itemShop_space_gray"
      }
    >
      <span style={{ paddingLeft: "15px" }}>{id}</span>

      <Cell
        style={{ width: "100%" }}
        before={
          photoURL ? (
            <Link href={urlUser} target="_blank">
              <Avatar src={photoURL} />
            </Link>
          ) : (
            <Avatar src={"https://vk.com/images/camera_200.png"} />
          )
        }
        description={
          <Div className="itemMainTopUsersDescription">
            <Div className="itemMainTopUsersLike">
              <Div className="iconPornCoin16px" />
              <Div style={{ whiteSpace: "nowrap" }}>{formatCoins(amount)}</Div>
            </Div>
            <Div className="itemMainTopUsersLike">
              <Icon16Like onClick={() => $useItem(1, idVk)} fill="red" />
              <Div
                style={{
                  marginLeft: "5px",
                  marginRight: "5px",
                  marginTop: "2px",
                }}
              >
                +{likes}
              </Div>
              <Icon16Like onClick={() => $useItem(2, idVk)} fill="#E48900" />
              <Div style={{ marginLeft: "5px", marginTop: "2px" }}>
                -{dislikes}
              </Div>
            </Div>
          </Div>
        }
      >
        <Div
          style={{
            display: "flex",
            justifyContent: "start",
            alignItems: "center",
            flexDirection: "row",
            marginLeft: "10px",
          }}
        >
          <span
            style={{
              color: [, "green", "#D40000", "blue", "#800080"][vipNameColor],
              // marginLeft: "10px",
            }}
          >
            {firstName + " " + lastName}
          </span>
          {vipNameColor === 0 ? (
           ""
          ) : (
            <Icon12Verified
              onClick={openMenuVIP}
              style={{ marginLeft: "5px" }}
              className="animationIcons"
              fill={[, "green", "#D40000", "blue", "#800080"][vipNameColor]}
              width={16}
              height={16}
            />
          )}
        </Div>
      </Cell>
    </Div>
  );
};

export { TopUsersItem };
