import React, { useState, useEffect } from "react";
import { ModalRoot, ModalCard, FormLayout, Input } from "@vkontakte/vkui";

const ModalInventoryUseItem = ({
  targetIdInventory,
  iconItem,
  nameItem,
  setActiveModal,
  // onChangeHandlerId,
  $useItem,
  targetId,
  openAlert,
}) => {
  const [idUserInventory, setIdUserInventory] = useState(0);

  function onChangeHandlerId(e) {
    const value = e.target.value.trim();
    // targetId = value;

    setIdUserInventory(value);
    console.log("onChangeHandlerId idUserInventory", idUserInventory);
  }

  return (
    <ModalRoot activeModal="use-item">
      <ModalCard
        id="use-item"
        onClose={() => setActiveModal(null)}
        icon={iconItem}
        header={nameItem}
        caption={
          <>
            <FormLayout>
              <Input
                top="–£–∫–∞–∂–∏—Ç–µ —Å—Å—ã–ª–∫—É –Ω–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è"
                placeholder="https://vk.com/porncoin"
                onChange={onChangeHandlerId}
                type="text"
              />
            </FormLayout>
          </>
        }
        actions={[
          {
            title: "–ò—Å–ø–æ–ª—å–∑–æ–≤–∞—Ç—å",
            mode: "commerce",
            action: () => {
              if (idUserInventory === "" || idUserInventory === 0) {
                setActiveModal(null);
                openAlert("–í–≤–µ–¥–∏—Ç–µ —Å—Å—ã–ª–∫—É –Ω–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è", "red");
              } else {
                $useItem(targetIdInventory, null, idUserInventory);
              }
            },
          },
        ]}
      ></ModalCard>
    </ModalRoot>
  );
};

export { ModalInventoryUseItem };
