import React from "react";
import { Cell, Div, Avatar, Link, Button } from "@vkontakte/vkui";
import Icon12Fire from "@vkontakte/icons/dist/12/fire";
import Icon16Add from "@vkontakte/icons/dist/16/add";
import Icon16Like from "@vkontakte/icons/dist/16/like";

const TopGroupsItem = ({ idVk, id, name, rating, photoURL, addGroupTop, scheme, likes, dislikes, $useItem }) => {
  const urlGroup = "https://vk.com/club" + idVk;

  return (
    <Div
      className={
        scheme === "bright_light"
          ? "itemShop_bright_light"
          : "itemShop_space_gray"
      }
    >
      <span style={{ paddingLeft: "15px" }}>{id}</span>
      <Cell
        style={{ width: "100%" }}
        before={
          photoURL ? (
            <Link href={urlGroup} target="_blank">
              <Avatar src={photoURL} />
            </Link>
          ) : (
            <Avatar src={"https://vk.com/images/camera_200.png"} />
          )
        }
        description={
          <Div className="itemMainTopGroupsBalance">
            <Div className="itemMainTopUsersLike">
              <Icon16Like 
              // onClick={() => $useItem(1)}
              fill="red" />
              <Div

                style={{
                  marginLeft: "5px",
                  marginRight: "5px",
                  marginTop: "2px",
                }}
              >
                +{likes}
              </Div>
              <Icon16Like
              // onClick={() => $useItem(2)}
              fill="#E48900" />
              <Div style={{ marginLeft: "5px", marginTop: "2px" }}>
                -{dislikes}
              </Div>
            </Div>
          </Div>
        }
        asideContent={
          <Div id={id} onClick={addGroupTop}>
            <Icon16Add fill="green" />
          </Div>
        }
      >
        <Div style={{ marginLeft: "10px" }}>{name}</Div>
      </Cell>
    </Div>
  );
};

export { TopGroupsItem };
